/* ==========================================================================
   Grid System responsive style - begin
   ========================================================================== */

@include laptop-landscape-medium {
	
	/*
	* If you changed some value for space do the same thing in masonry-type, product-list-main-and-vc scss files
	*/
	$columns_space_label: ('huge' );
	$columns_space_width: (25);
	
	@for $i from 0 to length($columns_space_label) {
		.edge-#{nth($columns_space_label,$i+1)}-space {
			$column_width: nth($columns_space_width,$i+1);
			$column_width_margin: $column_width * 2;
			
			&.edge-disable-bottom-space {
				margin-bottom: -#{$column_width_margin}px;
			}
			
			.edge-outer-space {
				margin: 0 -#{$column_width}px;
				
				.edge-item-space {
					padding: 0 #{$column_width}px;
					margin: 0 0 #{$column_width_margin}px;
				}
			}
		}
	}
}

@include phone-landscape {
	.edge-two-columns-form-without-space,
	.edge-two-columns-form-with-space  {
	    
	    .edge-column-left,
	    .edge-column-right {
	        display: table-row;
	        width: 100%;
	    }

	}
}

/* ==========================================================================
   Grid System responsive style - end
   ========================================================================== */